<template>
  <view>
    <!-- 轮播图 -->
    <view class="swiper-box">
      <swiper-component :swiperList="swiperList"></swiper-component>
    </view>
    <view class="recommend-item">
      <!-- 商品大图 -->
      <view class="recommend-item-img-box">
        <view class="max-img-box">
          <image src="@/static/img/Children.jpg" class="max-img" mode=""></image>
        </view>
        <view class="min-img-box">
          <view class="min-img-item">
            <image src="@/static/img/Children1.jpg" mode=""></image>
          </view>
          <view class="min-img-item">
            <image src="@/static/img/Children2.jpg" mode=""></image>
          </view>
          <view class="min-img-item">
            <image src="@/static/img/Children3.jpg" mode=""></image>
          </view>
        </view>
      </view>

      <view class="recommend-item-img-box">
        <view class="max-img-box">
          <image src="@/static/img/Furnishing.jpg" class="max-img" mode=""></image>
        </view>
        <view class="min-img-box">
          <view class="min-img-item">
            <image src="@/static/img/Furnishing1.jpg" mode=""></image>
          </view>
          <view class="min-img-item">
            <image src="@/static/img/Furnishing2.jpg" mode=""></image>
          </view>
          <view class="min-img-item">
            <image src="@/static/img/Furnishing3.jpg" mode=""></image>
          </view>
        </view>
      </view>
      <!-- 卡片组件 -->
      <card title="猜你喜欢"></card>
      <!-- 瀑布流 -->
      <view class="waterfall-container">
        <custom-waterfalls-flow :value="list">
          <!-- #ifdef MP-WEIXIN -->
          <view class="item" v-for="(item,index) in list" :key="index" slot="slot{{index}}">
            <view class="title">{{item.title}}</view>
            <view class="desc">
              <view class="price"><text>￥{{item.desc}}</text><text>￥{{item.desc*0.6}}</text></view>
              <view class="discount"><text>6折</text></view>
            </view>
          </view>
          <!-- #endif -->

          <!-- #ifndef MP-WEIXIN -->
          <template v-slot:default="item">
            <view class="item">
              <view class="title">{{item.title}}</view>
              <view class="desc">
                <view class="price"><text>￥{{item.desc}}</text><text>￥{{item.desc*0.6}}</text></view>
                <view class="discount"><text>6折</text></view>
              </view>
            </view>
          </template>
          <!-- #endif -->
        </custom-waterfalls-flow>
      </view>
    </view>
  </view>
</template>

<script>
  export default {
    name: "recommend",
    data() {
      return {
        swiperList: [
          'https://cdn.uviewui.com/uview/swiper/swiper3.png',
          'https://cdn.uviewui.com/uview/swiper/swiper2.png',
          'https://cdn.uviewui.com/uview/swiper/swiper1.png',
        ],
        list: [{
            image: '@/static/img/commodity1',
            title: '我是标题',
            desc: 341
          },
          {
            image: '@/static/img/commodity2',
            title: '我是标题我是标题我是标题我是标题我是标题我是标题我是标题我是标题我是标题',
            desc: 112
          },
          {
            image: '@/static/img/commodity3',
            title: '我是标题3',
            desc: 115
          },
          {
            image: 'https://via.placeholder.com/200x300.png/9400D3',
            title: '我是标题4',
            desc: 573
          },
          {
            image: 'https://via.placeholder.com/100x240.png/B0E0E6',
            title: '我是标题5',
            desc: 221
          },
          {
            image: 'https://via.placeholder.com/140x280.png/7FFFAA',
            title: '我是标题6',
            desc: 777
          },
          {
            image: 'https://via.placeholder.com/40x60.png/EEE8AA',
            title: '我是标题7',
            desc: 352
          }
        ]
      };
    }
  }
</script>

<style lang="scss" scoped>
  .recommend-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    // padding: 20rpx;
    background-color: #fff;


    .recommend-item-img-box {
      margin-bottom: 30rpx;

      .max-img-box {
        border-radius: 20rpx 20rpx 0 0;
        overflow: hidden;

        .max-img {
          width: 650rpx;
          height: 300rpx;
          vertical-align: bottom;
        }
      }

      .min-img-box {
        overflow: hidden;
        display: flex;
        border-top: 0rpx !important;
        border: 1px solid #dee1e6;
        border-radius: 0 0 20rpx 20rpx;

        .min-img-item {
          width: 33.33%;

          image {
            width: 100%;
            height: 220rpx;
          }
        }
      }
    }
  }


  .item {
    width: 100%;
    text-align: center;
    font-size: 34rpx;

    .title {
      overflow: hidden;
      -webkit-line-clamp: 2;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
    }

    .desc {
      margin: 15rpx;

      .price {
        margin-bottom: 15rpx;

        text {
          margin: 8rpx;
        }

        text:nth-child(2) {
          margin: 0;
          font-size: 26rpx;
          color: #c5c5c5;
          text-decoration: line-through;
        }
      }


      .discount {

        text {
          font-size: 26rpx;
          padding: 0 15rpx;
          color: #cf3340;
          border: 1rpx solid #cf3340;
          border-radius: 8rpx;
        }
      }
    }
  }
</style>
